
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
name: 'Question',
components: {
XHeader,
Group,
XInput,
XButton,
PopupPicker
},
data() {
return {
questionList: [],
question1: [],
question2: [],
question3: [],
answer1: '',
answer2: '',
answer3: ''
};
},
async created() {
await this.getData();
},
methods: {
async getData() {
let res = await this.$http('/queryQuestionList');
res.returnList.map(x => {
x.name = x.questionContent;
x.value = x.questionId;
});
this.questionList = [res.returnList];




},
async sub() {
if (
!this.question1.length ||
!this.question2.length ||
!this.question3.length
) {
this.$vux.toast.show({
type: 'text',
text: '',
time: 1500
});
return;
}
if (!this.answer1 || !this.answer2 || !this.answer3) {
this.$vux.toast.show({
type: 'text',
text: '',
time: 1500
});
return;
}

let res = await this.$http('/addQuestionAndAnswerForUser', {
body: {
questionIdList:
this.question1[0] +
',' +
this.question2[0] +
',' +
this.question3[0],
answer1: this.answer1,
answer2: this.answer2,
answer3: this.answer3
}
});
if (res.returnCode !== '0000') {
this.$vux.toast.show({
type: 'warn',
text: res.returnMessage,
time: 1500
});
} else {
this.$vux.toast.show({
type: 'success',
text: '',
time: 1500
});
this.$router.go(-1);
}
}
}
};



